import "./Login.scss";
import { PureComponent } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Button from "../Button";

class Login extends PureComponent {
    state = {
        isShowMore: true,
        login: '',
        password: '',
    }
    
    handleUserLogin = (e) => {
        this.setState({ login: e.target.value })
    }
    handleUserPassword = (e) => {
        this.setState({ password: e.target.value })
    }
    handleClick = () => {
        this.props.history.push('/');
    }
    validateForm = () =>{
        this.state.login === 'admin' && this.state.password === 'admin'? this.props.history.push('/admin'):document.getElementsByClassName("loginError")[0].style.display = "block";
    }

    render() {
        const { theme } = this.props;
        const { isShowMore } = this.state;
        const {login} = this.state;
        const {password} = this.state;
        return (
            <div>
                <div className="demoForm">
                <label className="loginError">Неправильно введен логин или пароль!{<br/>}(для теста логин: admin, пароль: admin)</label>
                    <div>
                        <label htmlFor="email">Введите логин</label>
                        <input type="text" required className="login__form form-control" name="логин"
                            placeholder="логин"
                            onChange={this.handleUserLogin} />
                    </div>
                    <div>
                        <label htmlFor="password">Введите пароль</label>
                        <input type="password" className="login__form form-control" name="password"
                            placeholder="пароль"
                            onChange={this.handleUserPassword} />
                    </div>
                    <Button text="Войти на страницу как администратор" className={`button__${theme.theme} login__button`}  onClick={this.validateForm} disabled={!login || !password} />
                </div>
                <div className="admin__block">
                    <Button text="вернуться на главную" className={`button__${theme.theme}`} onClick={this.handleClick} />
                </div>
            </div>
        )
    }
}
const mapStateToProps = ({ theme }) => ({
    theme: theme,
});

export default connect(mapStateToProps)(withRouter(Login))