import { PureComponent } from "react";
import { withRouter } from 'react-router-dom';
import { connect } from "react-redux";
import { Link, animateScroll as scroll } from 'react-scroll';

class HeaderLogo extends PureComponent {
    handleClick = () => {
        this.props.history.push('/');
    }
    render() {
        const { companyName } = this.props;
        return (
            <Link onClick={this.handleClick} to="home" spy={true} smooth={true} offset={-70} duration={500} style={{ textDecoration: 'none' }}> ТУРИСТИЧЕСКОЕ АГЕНТСТВО {companyName.companyName}
            </Link>
        )
    }
}

const mapStateToProps = ({ companyName }) => ({
    companyName: companyName,
});
export default connect(mapStateToProps)(withRouter(HeaderLogo));