import { PureComponent } from 'react';
// import Item from "../Item";
import './Delete.scss'
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { deleteDirection } from "../../../../actionCreators"
import Button from "../../../Button"

class Delete extends PureComponent {
  state = {
    value: ''
  }
  onItemRemove = itemId => {
    console.log(Number(this.state.value))
    const { deleteDirection } = this.props;
    deleteDirection(Number(this.state.value));
    this.setState({ value: '' });
  };
  handleChange = (e) => {
    this.setState({ value: e.target.value });
  }
  render() {
    const { directions } = this.props;
    const { theme } = this.props;
    const { value } = this.state;
    return (
      <div className='wrapper-add'>
        {/* мой первый вариант списка*/}
        {/* {directions.map(item => (
          <Item
            id={item.id}
            text={item.country}
            onRemove={this.onItemRemove}
          />
        ))} */}

        <label>
          <br />
          <select className='delete' value={this.state.value} onChange={this.handleChange}>
            <option selected>выберите страну</option>
            {directions.map(item => (
              <option
                id={item.id}
                value={item.id}
              >{item.country}</option>
            ))}
          </select>
          <Button className={`button__${theme.theme}`} text="удалить" onClick={this.onItemRemove} disabled={!value} />
        </label>
      </div>
    )
  }
}
const mapStateToProps = ({ directions, theme }) => ({
  directions: directions.directions,
  theme: theme,
});
const mapDispatchToProps = (dispatch) => ({
  deleteDirection: (direction) => dispatch(deleteDirection(direction)),
});

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Delete));