import './Theme.scss'
import { PureComponent } from 'react';
import { connect } from 'react-redux';
import Button from "../../../Button";
import { themeStyle } from "../../../../actionCreators"


class Theme extends PureComponent {
  state = {
    isShowMore: false,
    theme: ""
  }

  componentDidMount = () => {
    this.setState({ theme: this.props.theme });
  }

  handleClick = () => {
    this.setState({ isShowMore: !this.state.isShowMore });
    this.state.isShowMore ? this.state.theme = "orange" : this.state.theme = "blue";
    const { themeStyle } = this.props;
    const { theme } = this.state;
    themeStyle(theme);
  }

  render() {
    const { isShowMore } = this.state;
    const { theme } = this.props;
    return (
      <div className='theme'>
        <div>Cейчас включена {theme.theme === "blue" ? "синяя" : "оранжевая"} тема, хотите переключить на <div className={theme.theme !== "blue" ? `theme__blue`:`theme__orange`} onClick={this.handleClick}> {theme.theme !== "blue" ? "синюю тему" : "оранжевую тему"}?</div>
      </div></div>
    )
  }
}
const mapStateToProps = ({ theme }) => ({
  theme: theme,
});
const mapDispatchToProps = (dispatch) => ({
  themeStyle: (theme) => dispatch(themeStyle(theme)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Theme);