const initState = {
  footerblock: [
    { id: 0, title: "Принцип нашей работы", text: "Клиент должен быть на 100% доволен отдыхом, начиная с этапа его планирования." },
    { id: 1, title: "Время работы", text: "Пн — Пт: с 11:00 до 20:00"},
    { id: 2, title: "Адрес", text: "ул. ************ д. ***" },
  ],
}

function reducer(state = initState, action) {
  switch (action.type) {
    case 'FOOTER_BLOCK_FETCH_DIRECTIONS': {
      const newfooterblock = [...state.footerblock];
      newfooterblock.push(action.payload.footerblocks);
      return {
        ...state,
        footerblock: newfooterblock,
      }
    }
    case 'DEL_FOOTER': {
      const id = action.payload.id;
      const newFooter = [...state.footerblock];
      const delFooter = newFooter.filter(item => item.id !== id)
      return {
        ...state,
        footerblock: delFooter,
      }
    }

    default: return state;
  }
}

export default reducer;
